h1,h2,h3,h4,div,a {
    font-family: $tipografia-sans-serif;
}

.header {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    min-height: 10vh;
    height: auto;
    background-color: $color-fondo-header;
    color: $color-letra-header;

    .header__titulo {
        grid-column: 2/9;
        height: 100%;
        background-color: red;;
    }

    .header__nav {
        grid-column: 9/12;
        height: 100%;
        background-color: blue;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .nav__hamburger {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            cursor: pointer;

            .nav__span {
                width: 30px;
                height: 4px;
                border-radius: 2px;
                background-color: black;
                margin: 2px;
                transition: all 0.5s ease-in;
                    
            }   
        }    

        .nav__hamburger:hover {
            .nav__span:nth-child(2){
                width: 60%;
                background-color: red;
            }
        }
    }
}
    